import { RendererCanvas, getImgFromURL } from '../../../lib'

export class Solution {
  public divHost: HTMLElement;

  renderer: Renderer;

  constructor(id: string) {
    this.divHost = document.getElementById(id)

    // Center align
    this.divHost.style.display = 'flex';
    this.divHost.style.alignItems = 'center';

    const div = document.createElement('div');
    div.style.marginLeft = 'auto';
    div.style.marginRight = 'auto';
    div.style.backgroundColor = 'rgba(0,0,0,0.1)';
    // div.style.backgroundColor = '#555';
    div.style.width = '80%';
    div.style.height = '600px';
    div.style.display = 'flex';
    this.divHost.appendChild(div);

    // TODO
    // You code goes here for one time operation


    const divCanvas = document.createElement('div');
    divCanvas.style.marginLeft = 'auto';
    divCanvas.style.marginRight = 'auto';
    // divCanvas.style.backgroundColor = '#222';
    divCanvas.style.width = '100%';
    divCanvas.style.height = '600px';
    div.appendChild(divCanvas);

    this.renderer = new Renderer(divCanvas);

    // this.fluidSystem = new FluidSimulation(div)

    // console.log(this.renderer);
    // !! you should execute it for rendering
    // this.start();
  }
  public destroy() {
    // this.fluidSystem.destroy();
    try {
      while (this.divHost && this.divHost.lastElementChild) {
          const lastChild = this.divHost.lastElementChild as Node;
          this.divHost.removeChild(lastChild);
      }
  } catch (error) {
      console.error('Error removing child elements:', error);
  }
  
  }
}

export class Renderer extends RendererCanvas {
  pts = []
  constructor(div: any) {
      super(div);
      this.canvas.style.position = 'relative';
      this.canvas.style.width = '100%';
      this.canvas.style.height = '100%';


      this.start();
  }
  public render(ctx: CanvasRenderingContext2D): void {
    // console.log(ctx)

    this.pts.forEach((p, i)=>{
      ctx.beginPath();
      ctx.arc(p[0], p[1], 5 * i * 0.5, 0, Math.PI * 2)
      ctx.closePath();
      ctx.fill();
    })

    

    
  }
  public mouseClick(x: number, y: number): void {
    this.pts.push([x,y])
  }
  public mouseDrag(x: number, y: number): void {

  }
  public mouseMove(x: number, y: number): void {

  }
}
